import React from 'react';
import { PageLayout } from './PageLayout';
import { ScreenContainer } from 'smooth-doc/components';
import { DocLayout } from './DocLayout';

export function PageWrapper({
  children,
  props: {
    data: { mdx },
    path
  }
}) {
  if (!mdx?.fields?.pageType) return children;
  switch (mdx.fields.pageType) {
    case 'doc':
      return (
        <DocLayout
          title={mdx.fields.title}
          tableOfContents={mdx.tableOfContents}
          editLink={mdx.fields.editLink}
          path={path}
        >
          {children}
        </DocLayout>
      );
    case 'page':
      return (
        <PageLayout title={mdx.fields.title} type="page">
          <ScreenContainer>{children}</ScreenContainer>
        </PageLayout>
      );
    default:
      return children;
  }
}
