import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import styled, { x, down, css } from '@xstyled/styled-components';
import { ScreenContainer, NavLink } from 'smooth-doc/components';
import { DocSearch } from './DocSearch';
import { AppNav } from './AppNav';

const AppHeaderQuerySiteMetadata = graphql`
  query AppHeaderQuerySiteMetadata {
    logos: allFile(
      filter: {
        sourceInstanceName: { in: ["image", "default-image"] }
        name: { glob: "logo-nav*" }
      }
      sort: { fields: sourceInstanceName, order: DESC }
    ) {
      nodes {
        name
        publicURL
      }
    }
    site {
      siteMetadata {
        title
        docSearch {
          apiKey
          indexName
        }
      }
    }
  }
`;

const OuterHeader = styled.header`
  background-color: background;
  padding: 0.6875rem 0;
  box-shadow: 0px 2px 4px rgba(51, 51, 51, 0.1);

  ${down(
    'sm',
    css`
      overflow-x: auto;
    `
  )}

  ${down(
    'lg',
    css`
      padding: 0.25rem 0;
    `
  )}
`;

const NavSkipLink = styled.a`
  text-decoration: none;
  left: -999px;
  position: absolute;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
  z-index: -999;
  color: primary;

  &:focus {
    background-color: background;
    border-radius: base;
    left: auto;
    top: auto;
    height: auto;
    width: max-content;
    overflow: auto;
    padding: 2;
    margin: 2;
    text-align: center;
    z-index: 999;
  }
`;

function useLogo(logos) {
  return (
    logos.find(logo => logo.name === 'logo-nav-light') ||
    logos.find(logo => logo.name === 'logo-nav') ||
    null
  );
}

export function AppHeader() {
  const data = useStaticQuery(AppHeaderQuerySiteMetadata);
  const logo = useLogo(data.logos.nodes);
  return (
    <OuterHeader>
      <ScreenContainer px="6">
        <NavSkipLink tabindex="0" href="#main">
          Skip to content
        </NavSkipLink>
        <x.div
          row
          justifyContent="space-between"
          alignItems="center"
          flexWrap="nowrap"
          mx={-2}
        >
          <x.div
            flex={{ xs: '1 1 0', lg: '0 1 auto' }}
            px={2}
            display="flex"
            alignItems="center"
          >
            <NavLink
              row
              display="inline-flex"
              alignItems="center"
              flexWrap="nowrap"
              flexGrow="0"
              mx={-2}
              to="/"
              fontSize={{ xs: 24, lg: 48 }}
            >
              <x.img
                col="auto"
                px={2}
                height={32}
                src={logo.publicURL}
                alt={data.site.siteMetadata.title}
              />{' '}
              <x.span ml="2" fontFamily="cabin" fontSize={{ xs: 10, lg: 20 }}>
                by
              </x.span>
              <x.span ml="1" fontFamily="cabin" fontSize={{ xs: 16, lg: 20 }}>
                Axiomatics
              </x.span>
            </NavLink>
          </x.div>
          <x.div flex="0 1 auto" px={2}>
            <DocSearch {...data.site.siteMetadata.docSearch} />
          </x.div>
          <AppNav flex="0 1 auto" px={2} />
        </x.div>
      </ScreenContainer>
    </OuterHeader>
  );
}
